import skeleton from '@/components/easy-skeleton/easy-skeleton'
import ppt from '@/components/ppt/ppt.vue'
import Mescroll from "@/components/mescroll-uni/mescroll-uni.vue";
import pullList from '@/mixin/list'
import lazyImg from '@/components/lazyImg/lazyImg.vue'
import star from '@/components/star.vue'
import {
	getImgSrc
} from '@/utils/comUtils.js'
import {
	getListBanner
} from '@/api/index.js'
export default {
	mixins: [pullList],
	components: {
		skeleton,
		ppt,
		Mescroll,
		lazyImg,
		star
	},
	data() {
		return {
			showSkeleton: true,
			banner: [],
			// 筛选吸顶容器
			top: 0, //吸顶容器top
			active: false, //吸顶是否处于激活状态
			address: '全部', //地址回显
			addressVal: '', //地址列表参数
			hotVal: '', //热度列表参数
			priceVal: '', //价格列表参数
			filter: 'hot', //选中
			filterItem: {
				hot: [{
					title: 'high',
					value: 'high',
					active: false
				}, {
					title: 'down',
					value: 'down',
					active: false
				}],
				address: [{
					title: '全部',
					value: '43,56',
					active: true
				}, {
					title: '池北区',
					value: '43',
					active: false
				}, {
					title: '池西区',
					value: '56',
					active: false
				}],
				price: [{
					title: '不限',
					value: {
						'人均': ''
					},
					active: false
				}, {
					title: '从高到低',
					value: {
						'人均': 'desc'
					},
					active: false
				}, {
					title: '从低到高',
					value: {
						'人均': 'asc'
					},
					active: false
				}],
			},

			listData: [], //下拉列表

		}
	},
	onReady() {
		this.getBanner();
		// this.$refs['skeleton'].init()
		// setTimeout(() => {
		// 	this.showSkeleton = false
		// }, 1000)
	},
	onLoad(params) {
		uni.setNavigationBarTitle({
			title: params.title
		})
		this.params.categoryIds = params.categoryIds
	},
	methods: {
		getBanner() {
			getListBanner({
				categoryIds: 70,
				isTop: 'desc'
			}).then(res => {
				if (res.data.success) {
					let data = res.data.obj;
					// 图片数据处理
					data.forEach(item => {
						item.atts = getImgSrc(item.atts,0)
					})
					this.banner = data;
				}
			})
		},
		showDialog(key) {
			// 筛选条件数据
			this.filter = key;
			// 计算吸顶容器top,激活筛选状态
			const query = uni.createSelectorQuery().in(this).select('.filterWrapper'); // 这里要加上select
			query.boundingClientRect(e => {



				this.active = true;
				this.top = e.top + 'px'
			}).exec();

		},
		hideDialog(load, key, val) {
			this.active = false;
			this.top = 0

			// 查询
			if (!load) return
			// 地区回显
			if (this.filter == 'address') {
				this.address = this.filterItem[this.filter][key].title
			}
			//筛选条件候选项激活
			this.filterItem[this.filter].forEach((item, index) => {
				if (index == key) {
					item.active = true
				} else {
					item.active = false
				}
			})

			// 参数对象
			if (this.filter == 'hot') {

			} else if (this.filter == 'address') {
				this.params.categoryIds = val
			} else {
				this.params.orderBy = val
			}

			this.mescroll.resetUpScroll();

		},
		currentTap(id) {
			console.log(id)
			uni.navigateTo({
				url:'/pagesA/detail/detail?id='+id
			})
		}
	}

}
